/**
* CSS file to hold styles that apply across the application
*/

@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

html,
body {
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
  scroll-behavior: smooth;
}

#portfolioButton {
  background: linear-gradient(to right, darkred, red);
  border: none;
  color: white;
  text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.7);
  transition: transform 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

#linkedinConnection {
  background-color: #0077b5;
}

#githubConnection {
  background-color: #24292e;
}

#emailConnection {
  background-color: #d44638;
}

.buttonConnection:hover {
  transform: scale(1.1) translateZ(0);
}

.responsive-card {
  width: 90%;
}

@media (min-width: 768px) {
  .responsive-card {
    width: 50%;
  }
}
